      description: '<p>Wir (mein Co-Skipper &amp; ich) segeln mit einer 40er Segelyacht (eine Elan Impression, Baujahr 2010) von Athen nach Preveza. Bevorzugt laufen wir bei passendem Wetter die längere Strecke südlich herum, alternativ passieren wir den Graben von Korinth.<br/>Wir würden gerne noch ein paar segellustige Leute mitnehmen: Spaß am mitmachen ist nötig - aber Segel-Kenntnisse sind *nicht* erforderlich =8-)</p> <p>Die Charter beläuft sich auf ca. 750,- (+ ca. 90,- NK für Transit-log und Versicherung) für 2 Wochen und 1 Koje, ggf. entsprechend für 1 Kabine (=2 Kojen).</p> <p>Dieses "Event" ist keine kommerzielle Veranstaltung, vielmehr eine private Unternehmung aus Spaß am Segeln: "Teilnehmen" klicken ist somit nicht verbindlich, sondern verstehe ich zunächst nur als Interessensbekundung.<br/>Wer Lust hat mitzukommen sagt also gerne zu oder schreibt mir per PN: wir werden uns ggf. unkompliziert treffen um Details zu klären.</p> ',
