      id: '30',
      name: 'Agile Game Night #39 - Daily Stand-up Game (ENGLISH game night) ',
      rsvp_limit: 15,
      date_in_series_pattern: false,
      status: 'upcoming',
      time: 1587486600000,
      local_date: '2020-04-21',
      local_time: '18:30',
