      description: '<p>This group is for people (of all skill levels) that want to play Billiards (8/9/10) ball. Currently we play at Bavaria Billiards, either in doubles or singles, depending on how many people join. The exact address is:</p> <p>Bavaria Billard Bar Gerhard Huber<br/>Landshuter Allee 87, 80637 München, Germany<br/><a href="https://maps.app.goo.gl/vemAHtjnaSfiKWAG8" class="linkified">https://maps.app.goo.gl/vemAHtjnaSfiKWAG8</a></p> <p>You should get there at the exact specified time, as the tables get full extremely quickly, and reserving it might not be possible depending on the interest. RSVP and we can hold a table for you. As we have to pay for the tables to reserve them, failure to show up twice will result in removal from the group. If the interest is much greater than the capacity, we\'ll switch to a different venue 😁</p> <p>The current venue also has an arcade. Cost should be between €8 to €10 per hour depending on how many people show up.</p> ',
