      link: 'https://www.meetup.com/Selbstbewusst-authentisch-und-leicht-leben/events/269665397/',
      description: '<p>5 Tage<br/>5 Sessions<br/>5 x Inspiration</p> <p>In diesen 5 Tagen richtest Du Dein Business auf Erfolg aus.<br/>Du möchtest die Zeit während dieser Phase intensiv für Dein Business nutzen?<br/>Du möchtest endlich mehr Klarheit über Deine Strategie?</p> <p>In diesen 5 Tagen bekommst Du jeden Morgen in einer Live Online Session Input zu verschiedenen Themen wie Dein USP; Deine Vision, Deine Umsätze, Deine Produkte und Preise und im Anschluss erarbeitest Du individuell mit den von mir vorgestellten Materialien alles auf Dein Business abgestimmt.</p> <p>Wenn Du mal eine Session verpasst ist das gar nicht schlimm, Du bekommst im Anschluss die Aufzeichnungen zugeschickt und kannst Dir das auch später anschauen. Per E-Mail kannst DU Deine Fragen stellen und am Ende der Woche im Live Q&amp;A beantworte ich diese auch direkt persönlich.</p> <p>Diese Woche ist für Dich passend, wenn Du: neu mit Deinem Business starten möchtest, gerade nicht weisst, ob Du so wie es derzeit läuft auch weiter machen kannst, Du jetzt aufgrund der Corona Krise Deine Idee ausarbeiten möchtest oder auch um zu schauen, wo Du noch Potentiale hast, die Du noch gar nicht nutzt um zukünftig mehr Umsätze zu machen.</p> <p>Die Sessions finden per ZOOM statt. Den Link bekommst Du nach der Anmeldung.</p> <p>Anmelden kannst Du Dich direkt hier: <a href=\'https://www.digistore24.com/product/314695\' class=\'linkified\'>https://www.digistore24.com/product/314695</a></p> <p>Ich freue mich auf Dich!</p> <p>Deine Yvonne</p> ',
